:root {
  --dark: #08060d;
  --light-0: #dedeeb2c;
  --light-1: #f5f6f7;
  --light-2: #dfe6ec;
  --light-3: #ebedef;
  --light-4: #f3f3f4;
  --light-5: #e6e6e6;
  --light-6: #b9b9b9;
  --secondary: #787c7e;
  --light: #eef1f8;
  --lighter: #f4f7fc;
  --primary: #1a92ff;
  --primary-light: #d9edff;
  --shadow: #dcdfe6;
  --green: #00bc87;
  --light-green: #bdffe9;
  --blue: #00c2ee;
  --light-blue: #c0f6ff;
  --yellow: #ffa800;
  --light-yellow: #ffe7bd;
  --red: #d00090;
  --light-red: #ffbdee;
  --orange: #ff4500;
  --light-orange: #ff4400a6;
  --lighter-orange: #ffded2;
  --purple: #5849b5;
  --purple-light: #dfdaff;
  --pink: #ea4c89;
  --pink-light: #ffe6ef;

  /* light theme */
  --post-container-bg: white;
  --posts-bg: white;
  --post-bg: white;
  --post-color: var(--dark);
  --post-border: white;
  --post-hover-bg: var(--light);
  /* images light */
  --img-container-bg: white;
  --img-title-color: var(--dark);
  --img-desc-bg: rgba(255, 255, 255, 0.952);
  --img-desc-color: var(--dark);
  /* videos */
  --videos-container-bg: white;
  --videos-title-color: var(--dark);
  /* .filter-controller */
  --filter-bg: white;
  --filter-shadow: rgba(0, 0, 0, 0.185);

  --filter-input-border-focus: var(--pink); //pink
  --filter-input-btn-bg-focus: var(--pink); //pink
  --filter-input-btn-color-focus: white; //white
  --filter-input-border: var(--light-2); //light-2
  --filter-input-bg: var(--light-1); //light-1
  --filter-input-color: var(--dark); //dark
  --filter-input-btn-bg: var(--light-6); //light-6
  --filter-input-btn-color: white; // white
  --filter-btns-btn-toggle-color: var(--light-6); //--light-6
  --filter-btns-btn-span-bg: var(--pink); //pink
  --filter-btns-btn-span-color: white; //white
  --filter-btns-btn-toggle-active-color: var(--pink); //pink
  /* show filters btn */
  --filters-show-btn-border: var(light-2); //
  --filters-show-btn-bg: white; //white
  --filters-show-btn-color: var(--pink); //pink
  --filters-show-btn-shadow: rgba(0, 0, 0, 0.123); //rgba(0, 0, 0, 0.123)

  /* subreddits */
  --sub-reddits-scrollbar-bg: var(--light-2); //light-2
  --sub-reddits-scroll-thumb-bg: var(--pink); //pink
  --sub-reddits-scroll-thumb-border: var(--light-2); //light-2
  --sub-reddit-bg: var(--light-5); //light-5
  --sub-reddit-color: var(--dark); //light-5
  --subreddits-clear-btn: var(--pink); //pink
  --subreddit-hover-bg: var(--pink-light);
  --subreddit-hover-color: var(--pink);

  --notification-bg: white;
  --notification-color: var(--dark);
  --notification-shadow: #ea4c8956;
  --notification-border: #ea4c899d;
  --notification-close-btn-bg: var(--pink-light);
  --notification-close-btn-color: var(--pink);
  --notification-close-btn-hover-bg: var(--pink);
  --notification-close-btn-hover-color: white;

  --main-bg: white;
  --main-color: var(--dark);
  --main-input-title-color: var(--orange);
  --main-input-focus-border: var(--light-orange);
  --main-input-focus-btn-bg: var(--orange);
  --main-input-border: var(--light-2);
  --main-input-bg: var(--light-1);
  --main-input-color: var(--dark);
  --main-input-btn-bg: var(--light-6);
  --main-input-btn-color: white;

  --footer-color: var(--dark);
  --footer-font-tag-color: var(--light);
  --footer-button-color: var(--dark);

  --code-color: var(--orange);

  --loader-bg: var(--light); //light-1
  --loader-color: var(--dark); //--dark

  --post-view-container: white;
  --post-view-h3: var(--dark);
  --post-view-close-btn-bg:  var(--light);
  --post-view-close-btn:  var(--dark);
  --post-view-count-bg:  var(--light);
  --post-view-count:  var(--dark);
  --post-view-upvotes-bg:  var(--light);
  --post-view-upvotes:  var(--dark);
  --post-view-left-btn-bg:  var(--light);
  --post-view-left-btn:  var(--dark);
  --post-view-right-btn-bg:  var(--light);
  --post-view-right-btn:  var(--dark);
}

[data-theme="dark"] {
  /* posts dark mode */
  --post-container-bg: #2f3136;
  --posts-bg: #2f3136;
  --post-bg: #36393f;
  --post-color: #fff;
  --secondary: #dcddde;
  --post-border: #2f3136;
  --post-hover-bg: #2f3136;
  /* images */
  --img-container-bg: #2f3136;
  --img-title-color: #dcddde;
  --img-desc-bg: #2f3136;
  --img-desc-color: white;
  /* videos */
  --videos-container-bg: #2f3136;
  --videos-title-color: #dcddde;
  /* filter-controler */
  --filter-bg: var(--dark);
  --filter-shadow: rgba(0, 0, 0, 0.185);
  --filter-input-border-focus: #242424; //pink
  --filter-input-btn-bg-focus: var(--red); //pink
  --filter-input-btn-color-focus: white; //white
  --filter-input-border: #242424; //light-2
  --filter-input-bg: #242424; //light-1

  --filter-input-color: white; //dark
  --filter-input-btn-bg: #4d4d4d; //light-6
  --filter-input-btn-color: white; // white

  --filter-btns-btn-toggle-color: #4d4d4d; //--light-6
  --filter-btns-btn-span-bg: var(--red); //pink
  --filter-btns-btn-span-color: white; //white
  --filter-btns-btn-toggle-active-color: white; //pink

  /* show filters btn */
  --filters-show-btn-border: var(--dark); //
  --filters-show-btn-bg: var(--dark); //white
  --filters-show-btn-color: white; //pink
  --filters-show-btn-shadow: rgba(0, 0, 0, 0.123); //rgba(0, 0, 0, 0.123)

  /* subreddits */
  --sub-reddits-scrollbar-bg: #242424; //light-2
  --sub-reddits-scroll-thumb-bg: var(--red); //pink
  --sub-reddits-scroll-thumb-border: #242424; //light-2
  --sub-reddit-bg: #242424; //light-5
  --sub-reddit-color: white; //light-5
  --subreddits-clear-btn: var(--red); //pink
  --subreddit-hover-bg: #d00092da;
  --subreddit-hover-color: white;

  --notification-bg: var(--dark);
  --notification-color: white;
  --notification-shadow: #4d4d4d;
  --notification-border: none;
  --notification-close-btn-bg: #d0009260;
  --notification-close-btn-color: var(--red);
  --notification-close-btn-hover-bg: var(--red);
  --notification-close-btn-hover-color: white;

  --main-bg: var(--dark);
  --main-color: white;
  --main-input-title-color: var(--red);
  --main-input-focus-border: #242424;
  --main-input-focus-btn-bg: var(--red);
  --main-input-border: #242424;
  --main-input-bg: #242424;
  --main-input-color: white;
  --main-input-btn-bg: #4d4d4d;
  --main-input-btn-color: white;

  --footer-color: white;
  --footer-font-tag-color: #4d4d4d;
  --footer-button-color: white;

  --code-color: var(--red);

  --loader-bg: #292b2f; //light-1
  --loader-color: white; //--dark


  --post-view-container: #2f3136;
  --post-view-h3: #fff;
  --post-view-close-btn-bg:  #36393f;
  --post-view-close-btn:  white;
  --post-view-count-bg:  #36393f;
  --post-view-count:  #dcddde;
  --post-view-upvotes-bg: #36393f;
  --post-view-upvotes: #dcddde;
  --post-view-left-btn-bg:  #36393f;
  --post-view-left-btn:  #dcddde;
  --post-view-right-btn-bg:  #36393f;
