html {
  overflow-x: hidden;
  font-family: "Inter";
  scroll-behavior: smooth;
}
body {
  overflow-x: hidden;
  font-family: "Inter";
  scroll-behavior: smooth;
}
button {
  &:focus {
    outline: none;
  }
}
input {
  outline: none;
}
optgroup {
  outline: none;
}
select {
  outline: none;
}
textarea {
  &:focus {
    outline: none;
  }
}
.bg-purple {
  background: var(--purple);
}
.title {
  font-weight: 700;
  margin-bottom: 20px;
}

/* posts */
.posts-container {
  padding-top: 20px;
  padding-bottom: 20px;
  background: var(--post-container-bg);
  .inner {
    width: 40vw;
  }
  .title {
    margin-left: 0px;
    font-size: 22px;
  }

  .posts {
    column-count: 1;
    column-gap: 2px;
    position: relative;
    border: solid 2px var(--post-border);
    background: var(--posts-bg);

    .post {
      break-inside: avoid;
      background-color: transparent;
      border-bottom: solid 2px var(--post-border);
      padding: 30px;
      cursor: pointer;
      background: var(--post-bg);
      transition: 0.2s;
      /*  &:first-child {
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
      }
      &:last-child {
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px;
      } */
      &:hover {
        background-color: var(--post-hover-bg);
      }
    }
    img {
      border-radius: 8px;
      margin-bottom: 10px;
    }
    p {
      font-size: 18px;
      font-weight: 600;
      color: var(--post-color);
    }
    small {
      font-size: 11px;
      font-weight: 500;
      color: var(--secondary);
    }
  }
}

.post-view-container {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background: var(--post-view-container); //white
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 5;
  .inner {
    max-width: 50vw;
    img {
      border-radius: 20px;
    }
    h3{
      font-family: 'Inter';
      font-weight: 700;
      color: var(--post-view-h3); //dark
    }
  }
  .close-btn {
    position: fixed;
    top: 20px;
    right: 20px;

    width: 60px;
    height: 60px;
    border: none;
    border-radius: 50px;
    background: var(--post-view-close-btn-bg); //background: var(--light);
      color: var(--post-view-close-btn); //color: var(--dark);
  }
  .count{
      position: fixed;
      top: 20px;
      left: 20px;
      right: calc(50vw - 60px);
     
      padding: 15px;
      padding-left: 20px;
      padding-right: 20px;

      border: none;
      background: var(--post-view-count-bg); //background: var(--light);
      color: var(--post-view-count); //color: var(--dark);

      border-radius: 20px;
    }
  .upvotes{
    position: fixed;
    height: 70px;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
     
     button{
      padding: 15px;
      padding-left: 20px;
      padding-right: 20px;

      border: none;
      background: var(--post-view-upvotes-bg); //background: var(--light);
      color: var(--post-view-upvotes); //color: var(--dark);

      border-radius: 20px;
     }
    }
  .left-btn {
    position: fixed;
    top: 20vh;
    bottom: 20vh;
    left: 0;

    width: 70px;
    height: 60vh;
    border: none;
    background: var(--post-view-left-btn-bg); //background: var(--light);
    color: var(--post-view-left-btn); //color: var(--dark);
    
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
  }
  .right-btn {
    position: fixed;
    top: 20vh;
    bottom: 20vh;
    right: 0;

    width: 70px;
    height: 60vh;

    border: none;
    background: var(--post-view-right-btn-bg); //background: var(--light);
    color: var(--post-view-right-btn); //color: var(--light);

    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
  }
}

/* images */
.images-container {
  padding-top: 10px;
  padding-bottom: 20px;
  background-color: var(--img-container-bg);
  .title {
    color: var(--img-title-color);
  }
  .images {
    column-count: 3;
    column-gap: 20px;
    row-gap: 0;
    .img {
      break-inside: avoid;
      background: none;
      border-radius: 30px;
      margin-bottom: 20px;
      position: relative;
      cursor: pointer;
      .desc {
        position: absolute;
        bottom: -5px;
        left: 10px;
        right: 10px;
        padding: 15px;
        border-radius: 30px;
        background: var(--img-desc-bg);
        color: var(--img-desc-color);
        font-weight: 500;
        font-size: 15px;
        visibility: hidden;
      }
    }
    img {
      border-radius: 30px;
    }
  }
}

.fixed-main {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}
.scaled-img {
  transform: scale(1.5);
}
.image-view {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  backdrop-filter: blur(10px);
  overflow: hidden;
  z-index: 3;
  .image-scroll-container {
    img {
      &:hover {
        cursor: zoom-in;
      }
    }
    position: fixed;
    overflow-x: hidden;
    overflow-y: auto;
    background: rgb(0, 0, 0);
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
  }
  .fixed-loading-container {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.445);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .caption-container {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    padding-left: 20px;
    align-items: center;
    &:hover {
      > .caption {
        visibility: visible;
      }
    }
  }
  .caption {
    padding: 15px;
    border-radius: 10px;
    font-weight: 500;
    font-size: 15px;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.144);
    background-color: rgba(0, 0, 0, 0.589);
    color: white;
    max-width: 600px;
  }
  .close-btn {
    border: none;
    height: 60px;
    width: 60px;
    font-size: 17px;
    background-color: rgba(0, 0, 0, 0.445);
    color: white;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 3;
  }
  .count {
    border: none;
    border-radius: 10px;
    height: 50px;
    padding-left: 15px;
    padding-right: 15px;
    background-color: rgba(0, 0, 0, 0.445);
    color: white;
    position: fixed;
    top: 30px;
    left: 30px;
    z-index: 3;
  }
  .count-btns {
    border: none;
    height: 60%;
    width: 100px;
    background-color: rgba(0, 0, 0, 0.445);
    color: white;
    font-size: 20px;
  }
  .count-btns.prev {
    position: fixed;
    top: 20%;
    bottom: 20%;
    left: 0;
    z-index: 3;
  }
  .count-btns.next {
    position: fixed;
    top: 20%;
    bottom: 20%;
    right: 0;
    z-index: 3;
  }
}

.empty-btn {
  border: none;
  height: auto;
  width: auto;
  background-color: transparent;
  color: var(--orange);
  font-weight: 700;
  border-radius: 5px;
  &:hover {
    color: var(--light);
  }
}

.max-image-zoom {
  img {
    &:hover {
      cursor: zoom-out;
    }
  }
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: auto;
  background: #000;
  z-index: 4;
}

/* Videos */
.videos-container {
  background: var(--videos-container-bg);
  padding-top: 20px;
  padding-bottom: 60px;
  .title {
    color: var(--videos-title-color);
  }
  .videos {
    .video {
      width: 100%;
      border-radius: 15px;
      margin-bottom: 20px;
      background: transparent;
      iframe {
        height: 300px !important;
        width: 100% !important;
        position: relative !important;
        border-radius: 15px;
      }
    }
  }
}

.filter-controller {
  position: fixed;
  bottom: 10px;
  right: 0;
  z-index: 2;
  display: grid;
  background-color: var(--filter-bg);
  padding: 10px;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  align-items: center;
  row-gap: 7px;
  box-shadow: 0 0 20px 0 var(--filter-shadow);
  width: 300px;
  transition: 0.4s;
  .search {
    position: relative;
    input {
      &:focus {
        border: solid 1px var(--filter-input-border-focus); //pink
        + {
          .search-btn {
            background-color: var(--filter-input-btn-bg-focus); //pink
            color: var(--filter-input-btn-color-focus); //white
          }
        }
      }
      transition: 0.3s;
      width: 100%;
      border: solid 1px var(--filter-input-border); //light-2
      height: 45px;
      padding-left: 20px;
      padding-right: 60px;
      background-color: var(--filter-input-bg); //light-1
      font-weight: 600;
      color: var(--filter-input-color); //dark
      border-radius: 10px;
      font-size: 15px;
    }
    .search-btn {
      transition: 0.3s;
      border: none;
      border-radius: 10px;
      width: 100%;
      height: 35px;
      width: 35px;
      background-color: var(--filter-input-btn-bg); //light-6
      color: var(--filter-input-btn-color); // white
      font-size: 15px;
      font-weight: 600;
      position: absolute;
      right: 0;
      margin: 5px;
    }
  }
  .filter-btns {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  button.toggle {
    background: none;
    color: var(--filter-btns-btn-toggle-color); //--light-6
  }
  button {
    border: none;
    border-radius: 10px;
    height: 40px;
    width: 40px;
    background-color: transparent;
    position: relative;
    font-weight: 800;
    font-size: 17px;
    span {
      position: absolute;
      top: 0;
      right: 0;
      border-radius: 50px;
      background-color: var(--filter-btns-btn-span-bg); //pink
      color: var(--filter-btns-btn-span-color); //white
      padding: 3px;
      font-size: 10px;
    }
  }
  button.toggle.active {
    color: var(--filter-btns-btn-toggle-active-color); //pink
  }
}

.filter-controller-show-btn {
  position: fixed;
  bottom: 16px;
  right: 25px;
  z-index: 2;
  display: grid;
  background-color: transparent;
  button {
    border: solid 1px var(--filters-show-btn-border); //light-2
    border-radius: 50px;
    height: 50px;
    width: 50px;
    background-color: var(--filters-show-btn-bg); //white
    color: var(--filters-show-btn-color); //pink
    box-shadow: 0 0 20px 0 var(--filters-show-btn-shadow); //rgba(0, 0, 0, 0.123)
  }
}

.sub-reddits {
  padding-bottom: 10px;
  width: 100%;
  max-height: 40vh;
  overflow-y: auto;
  text-align: left;
  &::-webkit-scrollbar {
    background: var(--sub-reddits-scrollbar-bg); //light-2
    border-radius: 50px;
    width: 10px;
  }
  &::-webkit-scrollbar-thumb {
    background: var(--sub-reddits-scroll-thumb-bg); //pink
    border: 2px solid var(--sub-reddits-scroll-thumb-border); //light-2
    border-radius: 50px;
  }
  .sub-reddit {
    font-size: 10px;
    background: var(--sub-reddit-bg); //light-5
    color: var(--sub-reddit-color);
    padding-left: 10px;
    padding-right: 10px;
    font-size: 12px;
    border-radius: 50px;
    height: 30px;
    width: auto;
    display: flex;
    align-items: center;
    margin-bottom: 5px;
    margin-right: 5px;
    &:hover {
      background-color: var(--subreddit-hover-bg); //var(--pink-light);
      color: var(--subreddit-hover-color); //var(--pink);
    }
  }
  .clear-btn {
    background-color: var(--subreddits-clear-btn) !important; //pink
    border: none;
    border-radius: 50px;
    height: 30px;
    position: relative;
    font-weight: 800;
    font-size: 11px;
    min-width: 90px;
    color: white;
  }
}

.main {
  background: var(--main-bg);
  color: var(--main-color);
  height: 100vh;
}

.filter-main-input {
  padding-top: 25vh;
  margin-bottom: 19vh;

  .title {
    font-family: "Poppins";
    font-size: 50px;
    color: var(--main-input-title-color); //var(--orange);
    margin-bottom: 0;
    position: sticky;
  }
  .search {
    position: relative;
    width: 100%;
  }
  input {
    &:focus {
      border: solid 1px var(--main-input-focus-border); //var(--light-orange);
      + {
        .search-btn {
          background-color: var(--main-input-focus-btn-bg); //var(--orange);
          color: white;
        }
      }
    }
    transition: 0.3s;
    width: 100%;
    border: solid 1px var(--main-input-border); //var(--light-2);
    border-radius: 100px;
    height: 60px;
    padding-left: 30px;
    padding-right: 60px;
    background-color: var(--main-input-bg); //var(--light-1);
    font-weight: 600;
    color: var(--main-input-color); //var(--dark);
    border-radius: 20px;
    margin-bottom: 20px;
  }
  .search-btn {
    transition: 0.3s;
    border: none;
    border-radius: 14.5px;
    width: 100%;
    height: 50px;
    width: 50px;
    background-color: var(--main-input-btn-bg); //var(--light-6);
    color: var(--main-input-btn-color); //white
    font-size: 20px;
    font-weight: 600;
    position: absolute;
    right: 0;
    margin: 5px;
  }
}
code {
  color: var(--code-color);
}

.notifications {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 320px;
  height: 100vh;
  overflow-y: auto;
  background: none;
  padding: 10px;
  &::-webkit-scrollbar {
    display: none;
  }
  .notification {
    width: 100%;
    padding: 10px;
    padding-left: 10px;
    padding-right: 15px;
    margin-bottom: 10px;
    background-color: var(--notification-bg);
    color: var(--notification-color);
    display: flex;
    align-items: center;
    box-shadow: 0 0 5px 0 var(--notification-shadow); //#ea4c8956
    border: 1px solid var(--notification-border); //#ea4c899d
    border-radius: 10px;
    height: 60px;
    z-index: 2;
    p {
      margin: 0;
      padding: 0;
      padding-left: 10px;
    }
    &:hover {
      > .close-btn {
        display: block;
      }
    }
    .close-btn {
      border: none;
      border-radius: 10px;
      width: 40px;
      height: 40px;
      padding-left: 15px;
      padding-right: 15px;
      background-color: var(--notification-close-btn-bg); //var(--pink-light);
      color: var(--notification-close-btn-color); //var(--pink);
      display: none;
      &:hover {
        border: none;
        border-radius: 10px;
        height: 40px;
        padding-left: 15px;
        padding-right: 15px;
        background-color: var(--notification-close-btn-hover-bg); //pink
        color: var(--notification-close-btn-hover-color); //white
      }
    }
  }
}

/* defaults */
.ver {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.ver-t-2 {
  column-count: 2;
}
.loader {
  border: none;
  border-radius: 10px;
  height: 40px;
  padding-left: 15px;
  padding-right: 15px;
  background: var(--loader-bg); //light-1
  color: var(--loader-color); //--dark
}
.loaded {
  border: none;
  border-radius: 10px;
  height: 40px;
  padding-left: 15px;
  padding-right: 15px;
  background: var(--loader-bg); //light-1
  color: var(--loader-color); //--dark
}
.pulse-loader {
  color: red;
}
.sticky-header {
  position: sticky;
}
.text-orange {
  color: var(--orange);
}

footer {
  padding-bottom: 20px;
  .top {
    p {
      font-size: 14px;
      color: var(--footer-color); //var(--dark)
      font-weight: 500;
      margin-top: 10px;
      font {
        font-size: 13px;
        color: var(--footer-font-tag-color); //var(--light)
        font-weight: 500;
      }
    }
  }
  .social-icons {
    button {
      border: none;
      background: none;
      font-size: 25px;
      color: var(--footer-button-color); //var(--dark)
    }
  }
}
a {
  color: inherit;
  &:hover {
    color: inherit;
    text-decoration: none;
  }
}
@media (min-width: 800px) {
  .images {
    .img {
      &:hover {
        > .desc {
          visibility: visible;
        }
      }
    }
  }
  .filter-controller.showing {
    left: calc(100% - 300px);
  }
  .filter-controller.hiding {
    left: 150%;
  }
}

/* mobile / tab */
@media (max-width: 800px) {
  .pc{
    display: none;
  }
  .posts {
    column-count: 1;
  }
  .posts-container {
    .title {
      margin-left: 20px;
    }
    .inner {
      width: 100%;
    }
    padding-top: 0px;
    padding-bottom: 40px;
  }
  .post-view-container {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 5;
    .inner {
      max-width: 90vw;
    }
    .close-btn {
      position: fixed;
      top: 10px;
      right: 10px;

      width: 60px;
      height: 60px;
      border: none;
      border-radius: 50px;
    }
    
    .left-btn {
      position: fixed;
      top: auto;
      bottom: 0;
      left: 0;

      width: 70px;
      height: 70px;

      border: none;

      border-top-right-radius: 20px;
      border-bottom-right-radius: 0px;
    }
    .right-btn {
      position: fixed;
      top: auto;
      bottom: 0;
      right: 0;

      width: 70px;
      height: 70px;

      border: none;

      border-top-left-radius: 20px;
      border-bottom-left-radius: 0px;
    }
  }

  .image-view {
    .image-scroll-container {
      align-items: center;
    }
    .count-btns {
      border: none;
      height: 60%;
      width: 50px;
      background-color: rgba(0, 0, 0, 0.034);
      color: white;
      font-size: 20px;
    }
    .caption {
      visibility: visible;
      transition: 3s;
    }
  }
  .images-container {
    padding-top: 5px;
    .images {
      column-count: 1;
      column-gap: 20px;
      row-gap: 0;
      .img {
        break-inside: avoid;
        background: none;
        border-radius: 30px;
        margin-bottom: 20px;
      }
    }
  }
  .videos {
    column-count: 1;
    column-gap: 20px;
    border-radius: 10px;
    iframe {
      border-radius: 10px;
    }
  }
  .filter-controller {
    position: fixed;
    bottom: 10px;
    right: 0;
    padding: 10px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    width: 100%;
    transition: 0.4s;
  }
  .filter-controller.showing {
    left: 0%;
    right: 0;
    bottom: 0;
  }
  .filter-controller.hiding {
    left: 110%;
    right: 0;
    bottom: 0;
  }
  .filter-controller-show-btn {
    position: fixed;
    z-index: 2;
    display: grid;
    background-color: transparent;
  }
  .sub-reddits {
    padding-bottom: 10px;
    width: 100%;
    max-height: 25vh;
    overflow-y: auto;
    text-align: left;
  }
}



.btn-key {
  border: none;
  border-radius: 7px;
  height: 30px;
  padding-left: 12px;
  padding-right: 12px;
  box-shadow:  0px 1.5px 0 2.5px #d8d8dc;
  background: white;
  color: #a2a2ab;
  font-size: 12px;
  font-weight: 700;
}